import React from 'react';
import store from './store.js';
import graphQLFetch from './graphQLFetch.js';

export default class About extends React.Component {
  static async fetchData() {
    const data = await graphQLFetch('query {about}');
    return data;
  }

  constructor(props) {
    super(props);
    const apiAbout = store.initialData ? store.initialData.about : null;
    delete store.initialData;
    this.state = { apiAbout };
  }

  async componentDidMount() {
    const { apiAbout } = this.state;
    if (apiAbout == null) {
      const data = await About.fetchData();
      this.setState({ apiAbout: data.about });
    }
  }

  render() {
    const { apiAbout } = this.state;

    return (
      <div className="text-center">
        <h3>Flora Fauna Sighting Map version 0.9</h3>
        <h4>
          {apiAbout}
        </h4>
        <small>
          <hr />
          <p className="text-center">
            Full source code available at this
            {' '}
            <a href="https://github.ccs.neu.edu/NEU-CS5610-SU21/TeamOrb-FloraFauna">
              GitHub repository
            </a>
          </p>
        </small>
      </div>

    );
  }
}
